'use strict';
import cards from './menu.json';
import cardsTemplates from './templates/card-templ.hbs';
import './styles.css';

const Theme = {
  LIGHT: 'light-theme',
  DARK: 'dark-theme',
};

const cardsList = {
  cardsMenu: document.querySelector('.js-menu'),
};
function buildMenu(cards) {
  const markup = cards.map(item => cardsTemplates(item)).join('');
  cardsList.cardsMenu.insertAdjacentHTML('beforeend', markup);
}

buildMenu(cards);

const change = document.querySelector('input.js-switch-input');

change.addEventListener('change', themeChange);

function themeChange(e) {
  if (e.target.checked) {
    document.body.classList.add(Theme.DARK);
    document.body.classList.remove(Theme.LIGHT);
  } else {
    document.body.classList.remove(Theme.DARK);
    document.body.classList.add(Theme.LIGHT);
  }
}
